@import "../../global.scss";

.topbar{
    width: 100%;
    height: 70px;
    background-color: white;
    color: $mainColor;
    position:fixed ;
    top: 0;
    z-index: 3;
    transition: all 1s ease;

.wrapper{

    padding: 10px 30px;
    // float: left;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left{
        // padding-left: 20%;
        display: flex;
        align-items: center;

        .logo{

            // font-size: 40px;
            // font-weight: 700;
            // text-decoration: none;
            // color: inherit;
            margin-left: 100px;
            margin-right: 80px;
            align-items: center;

            img{
                width: 70px;
                border-radius: 50%;
            }
        }
            .itemContainer{
                display: flex;
                align-items: center;
                margin-left: 40px;


                .icon{
                    font-size: 18px;
                    margin-right: 5px;

                }
                span{
                    font-size: 15px;
                    font-weight: 700;
                }
            }
        }

        .right{


            .hamburger{
                width: 32px;
                height: 25px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                

                span{
                    width: 100%;
                    height: 3px;
                    // background-color: white;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;
                }
            }
        }
}

&.active{
    background-color: white;
    color:$mainColor;

    .hamburger{
        span{
            &:first-child{
                background-color: rgb(59, 36, 36);
                transform: rotate(45deg);
            }
            &:nth-child(2){
                opacity: 0;
            }
            &:last-child{
                background-color: rgb(107, 49, 49);
                transform: rotate(-45deg);

            }
        }
    }
}
}