@import "../../global.scss";

.contact {
  background-color: white;
  display: flex;

  @include mobile {
    flex-direction: column;
  }

  .left {
    flex: 1;
    overflow: hidden;
    
    .icon{
      font-size: 18px;
      margin-right: 5px;

  }
    img {
      height: 100%;
    }
  }
  .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h1 {
      font-size: 50px;
      color: lightseagreen !important;
      overflow: hidden;

    }

    form {
      width: 40%;
      height: 90%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-around;
      border-radius: 25px;
      background-color: rgba(207, 202, 202, 0.438) !important;
      box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;;

      input {
        width: 300px;
        height: 40px;
        font-size: 14px;
        border-top: transparent;
        border-left: transparent;
        border-right: transparent;
        border-bottom: 2px solid rgb(19, 18, 18);
        background-color: transparent !important;
        

        @include mobile {
          width: 200px;
          height: 20px;
        }
      }

      textarea {
        width: 300px;
        height: 200px;
        font-size: 14px;

        border-top: transparent;
        border-left: transparent;
        border-right: transparent;
        border-bottom: 2px solid rgb(19, 18, 18);
        background-color: transparent !important;

        

        @include mobile {
          width: 200px;
          height: 100px;
        }
      }

      button {
        width: 180px;
        height: 40px;
        color: white;
        background-color: rgb(19, 102, 91);
        border: none;
        border-radius: 10px;
        font-weight: 500;
        cursor: pointer;
          
        box-shadow: rgba(139, 209, 192, 0.4) 0px 30px 90px;

        &:focus {
          outline: none;
        }
      }

      span {
        color: green;
      }
    }
  }
}