.intro{
    background-color: white;
    display: flex;
    

    .left{
        flex: 0.5;
        position: relative;
        overflow: hidden;


        .wrapper{
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            

            h1{
                font-size: 60px;
                margin: 10px;
            }
            h2{
                font-size: 35px;
            }
            h3{
                font-size: 30px;

                span{
                    font-size: inherit;
                    color: lightseagreen;
                }

                .ityped-cursor{
                    animation: blink 1s infinite;
                }
                @keyframes blink{
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }
            }
        }
        a{
            position:absolute;
            bottom: 10px;
            left: 50%;

            img{
                width: 30px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink{
                100%{
                    opacity: 0;
                }

            }
        }
    }

    .right{
        flex: 0.5;
        position: relative;
        overflow: hidden;
        
        
     
        .imgContainer{
           
            width: 700px;
            height: 700px;
            // background-color:white;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: left;

            img{
                width: 100%;
                justify-content: right;
               
            }
        }
    }
}