@import "../../global.scss";

.qualification{
    display: flex;

    .internship
    {
        flex: 0.5;
        position: relative;
        overflow: hidden;

        img{
            width: 30%;
            border-radius: 50%;
            
        }
    
        background-color: rgba(191, 220, 221, 0.644);
        margin-right: 15px;
        padding-right: 20px;
       
        h2{
            margin-left: 25px;
            font-size: 25px;
        }
    
    
        h4{
            padding-top: 5px !important;
            color: rgb(199, 142, 177) !important;
            font-size: 25px;
  
        }
        ul{
            list-style: square !important;
           @include mobile{
               
                padding-left: 15px !important;
            }
           
            li{
               
                h4{
                    margin-top: 5px !important;
                 
                }
                h5{
                    font-size: 20px;
                    margin: 0px;
                    color: rgb(160, 65, 112);
                }
                p{
                    font-size: 16px;
                    margin-top: 10px !important;
    
                }
            }
        }
    
       
    }
    
    .education
    {
        
        background-color: rgba(188, 247, 229, 0.904);
        padding-right: 20px;
        flex: 0.5;
        position: relative;
        overflow: hidden;

        h2{
            margin-left: 25px;
        }
    
        h4{
            padding-top: 5px !important;
            color:  rgb(199, 142, 177)  !important;
        
    
        }
        ul{
            list-style: square !important;
            @include mobile{
               
                padding-left: 15px !important;
            }
           
            li{
               margin-top: 0px;
                h4{
                    font-size: 20px;
                    padding-top: 0px !important;
                    font-weight: 700;
                }
                p{
                    font-size: 16px;
                    margin-top: 10px !important;
    
                }
            }
        }
    
       
    }
    
    }
     