@import "../../global.scss";
.about{
    display:justify;
    justify-content: center;
    // padding-left: 50px;
    background-color: white;

    @include mobile{
        flex-direction: column;
        align-items: center;
        padding-left: 20px;
    }
}
.AboutPage{
      
    h1{
        color: lightseagreen !important;
        font-size: 50px;
        margin-bottom: 0px;
        padding-left: 30px;
        // margin-left: 30px;
       
    
    }
     }
    
// .details{
//     flex: 0.5;
// }
.aboutsection{
    // display: flex;
    
    @include mobile{
        flex-direction: column;
        align-items: center;
    }
    


.ImageSection{
    // display: flex;
    // justify-content: center;
    // flex:0.5;
    
    // float: left;
    // width: 90%;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    .img{
        width: 100%;
        height: 50vh;
        img{
            width: 100%;
            object-fit: cover;
            height: 100%;
        }
    }

    .about-info{
        margin-left: 2rem;
        h4{
            font-size: 2rem;
        }
        .about-text{
            padding-left: .5rem 0;
            // float: left;
            //margin-top: 0;
            //width: 80%;
            text-align: center; 
            font-size: 25px;

            p{
                padding-top: 5px;
                padding-bottom:0px !important;
            }
        }
    }
    @media screen and (max-width: 1400px){
        flex-direction: column;
        .about-info{
            margin-left: 0;
            margin-top: 1rem;
        }
    }

    .about-details{
        display: flex;
        
        p{
            padding-top: 5px !important;
            justify-content: center;
        }
        .right-section{
            margin-left: 2.3rem;
        }
    }
    
    .btn{
        padding: 1rem 2rem;
        background-color: #037FFF;
        outline: none;
        border: none;
        font-family: inherit;
        font-size: inherit;
        color: white;
        text-transform: uppercase;
        cursor: pointer;
        letter-spacing: 2px;
        margin-top: 1rem;
        position: relative;
        &::after{
            position: absolute;
            content: "";
            width: 0;
            height: .3rem;
            left: 0;
            bottom: 0;
            background-color: #a4acc4;;
            transition: all 0.4s ease-in-out;
        }
        &:hover::after{
            width: 100%;
        }
    }
    
}


